import React from "react";
import styled from "styled-components";
import "../Layer.css";

const Layer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: black;
  width: 100%;
  height: 100vh;
  position: absolute;
  visibility: hidden;
  top: 0px;
  left: 0px;
  font-weight: bold;
  color: white;
`;

const LayerText = styled.div`
  font-size: 64px;
`;

const LayerHex = styled.div`
  margin-top: 1.5em;
  font-size: 32px;
`;

export default () => (
  <Layer className="layer">
    <LayerText className="layer__text">Copied!</LayerText>
    <LayerHex className="layer__hex" />
  </Layer>
);
